/* box avec bordure animée pour encadrer les projets*/
.project {
    background-color: #3b3646;
}
.p_liens {
    padding-bottom: 5%;  
    margin: auto;
}
.liens {    
    text-decoration: none;
    color: white;
}
.liens:hover{
    color:#a29ca9;
}
.containerproject {
    padding-top: 5%;
    padding-bottom: 5%;
    text-align:start;
}
.background-img {
    height: 10rem;
    width: 10rem;
    margin: 5% auto;
    padding:5%;
    border: 1px solid #2a3cad;
    border-radius: 4px;
    box-shadow: 0px 0px 5px #2a3cad;
    position:relative;
}      
.box {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 10rem;
    height: 10rem;
    background: #111845a6;
    box-sizing: border-box;
    overflow: hidden;
    box-shadow: 0 20px 50px rgb(23, 32, 90);
    border: 2px solid #2a3cad;
    color: white;
    padding: 5%;
}  
.box:before {
    content: "";
    position:absolute;
    top:0;
    left:-100%;
    width:100%;
    height:100%;
    background: rgba(255,255,255,0.1);
    transition:0.5s;
    pointer-events: none;
}  
.box:hover:before {
    left:-50%;
    transform: skewX(-5deg);
}  
.box .content {
    position:absolute;
    top:15px;
    left:15px;
    right:15px;
    bottom:15px;
    border:1px solid #f0a591;
    padding:5%;
    box-shadow: 0 5px 10px rgba(9,0,0,0.5);
    text-align:center;
    display: flex;
}  
.box span {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: block;
    box-sizing: border-box;    
}  
.box span:nth-child(1) {
    transform:rotate(0deg);
}  
.box span:nth-child(2) {
    transform:rotate(90deg);
}
  
.box span:nth-child(3) {
    transform:rotate(180deg);
}  
.box span:nth-child(4) {
    transform:rotate(270deg);
}  
.box span:before {
    content: "";
    position: absolute;
    width:100%;
    height: 2px;
    background: rgb(161, 133, 216);
    animation: animate 4s linear infinite;
}  
@keyframes animate {
    0% {
    transform:scaleX(0);
    transform-origin: left;
    }
    50% {
    transform:scaleX(1);
    transform-origin: left;
    }
    50.1% {
    transform:scaleX(1);
    transform-origin: right;      
    }    
    100% {
    transform:scaleX(0);
    transform-origin: right;      
    }
}